import React from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import Img from 'gatsby-image';
import styled from 'styled-components';

const HeroWrapper = styled.div`
  padding-top: 12rem;

  @media screen and (max-width: 720px) {
    padding-top: 7rem;
  }
`;

const Hero = () => {
  const data = useStaticQuery(graphql`
    query {
      heroImage: file(relativePath: {eq: "index/hero-main.png"}) {
        childImageSharp {
          fluid(maxHeight: 1080) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <>
      <section id="hero">
        <HeroWrapper className='row d-flex section-row ml-lg-3'>
          <div className='col-12 col-sm-12 col-md-6 col-lg-4 mt-md-5 order-0 order-lg-0 hero-text'>
            <div className='text-center text-md-left pl-md-2 pl-lg-5 mx-0 mx-sm-5 mx-md-0 px-0 px-sm-5 px-md-0'>
              <h1 className="d-inline my-lg-3"><span className='text-purple'>Интернет и телевизия,</span> неуловими и за светкавиците</h1>
              <hr className='d-block d-md-none center my-3' />
              <hr className='d-none d-md-block my-4' />
              <p className=" my-lg-4" style={{ whiteSpace: 'pre-wrap' }}>
                Хей, ти, вземи високоскоростен<br />GPON нет
                и интерактивна ТВ <br />на цени от <span className='text-blue-big'>22.99 лв./месец</span>
              </p>
              <div className='text-center text-md-left mt-4 mt-lg-5'>
                <Link to={'/za-doma'} className='btn btn-primary green my-3 mt-md-0 w-75'>Виж пакетите</Link>
              </div>
            </div>
          </div>
          <div className='d-none d-md-block col-12 col-md-6 col-lg-8 order-1 order-lg-1'>
            <Img fluid={data.heroImage.childImageSharp.fluid} className='hero-image' alt='Мъж и жена гледат телевизия' />
          </div>
        </HeroWrapper>
      </section>
    </>
  );
}

export default Hero;