import React, { useRef, useState } from 'react';
import SectionTitle from '../shared/section-titles';
import CountUp from 'react-countup';
import VisibilitySensor from 'react-visibility-sensor';

const VideoSection = () => {
  const startRef = useRef(null);
  const [clients, setClients] = useState(0);
  const [coverage, setCoverage] = useState(0);
  const [speed, setSpeed] = useState(0);
  const [homes, setHomes] = useState(0);
  const duration = 5;

  return (
    <>
      <section id='video' className='spacing-bottom center'>
        <div className='my-5'>
          <SectionTitle
            undertext={'И все пак, нека те запознаем с някои цифри, които са красноречиви за нашето развитие и успехи в тези над 20 години работа с телевизия и интернет.'}
          >
            <h2>Повече от статистиката <span className='text-purple'><br/>за нас говорят клиентите</span></h2>
            <hr className='center' />
          </SectionTitle>

          <VisibilitySensor
            onChange={(isVisible) => {
              if (isVisible) {
                setClients(18000);
                setCoverage(35);
                setSpeed(500);
                setHomes(22)
              }
            }}
            delayedCall
          >
            <div className='container mt-5'>
              <hr className='normal' />
              <div className='row d-flex justify-content-between text-center'>
                <div className='col-6 col-md-3'>
                  <h2 className='statistics'><CountUp start={0} end={clients} duration={duration} ref={startRef} />+</h2>
                  <h3>Приятели (клиенти)</h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 className='statistics'><CountUp start={0} end={coverage} duration={duration} /></h2>
                  <h3>Населени места</h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 className='statistics'><CountUp start={0} end={speed} duration={duration} />+ км</h2>
                  <h3>Оптична мрежа</h3>
                </div>
                <div className='col-6 col-md-3'>
                  <h2 className='statistics'><CountUp start={0} end={homes} duration={duration} /></h2>
                  <h3>Години в бранша</h3>
                </div>
              </div>
              <hr className='normal' />
            </div>
          </VisibilitySensor>
        </div>
      </section>
    </>
  )
}

export default VideoSection;